
/* --- Uptime --- */

#uptime {
    font-size: 12px;
    /* background: #fff; */
    /* border: 1px solid #e3e4e6; */
    /* border-radius: 5px; */
    box-shadow: 0px 2px 6px rgba(0, 0, 0, 0.05);
    overflow: hidden;
}

#uptime .item {
    padding: 18px 20px;
    border-bottom: 1px solid #e7e8e9;
}

#uptime .item:last-child {
    border-bottom: none;
}

#uptime .loading {
    background: url(./assets/loading.svg) center center / 40px 40px no-repeat;
    height: 100px;
}

/* #uptime .item:hover {
    background-color: #fffdf5;
} */

#uptime .meta {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 8px;
}

#uptime .meta .info {
    display: flex;
    align-items: center;
}

#uptime .meta .name {
    font-size: 16px;
    line-height: 20px;
    /* color: #5a606b; */
}

#uptime .meta .link {
    background: url(./assets/link.svg) center center / 100% 100% no-repeat;
    width: 13px;
    height: 13px;
    margin-left: 6px;
    opacity: 0.6;
    text-indent: -99999px;
    transition: opacity ease-out 300ms;
}

#uptime .meta .link:hover {
    opacity: 1;
}

#uptime .meta .status {
    background: left center / 14px auto no-repeat;
    padding-left: 20px;
    font-size: 14px;
}

#uptime .meta .status.ok {
    background-image: url(./assets/success.svg);
    color: #28a745;
}

#uptime .meta .status.down {
    background-image: url(./assets/fail.svg);
    color: #d9534f;
}

#uptime .meta .status.unknow {
    background-image: url(./assets/question.svg);
    color: #6a737d;
}

#uptime .timeline {
    height: 35px;
    display: flex;
    justify-content: space-between;
    margin-bottom: 10px;
}

#uptime .timeline i {
    flex-grow: 1;
    margin: 0 1px;
    cursor: pointer;
}

#uptime .timeline i:hover {
    opacity: 0.6;
}

#uptime .timeline i.ok {
    background: #9fd8ac;
}

#uptime .timeline i.none {
    background: #f3f3f3;
}

#uptime .timeline i.down {
    background: #f29571;
}

#uptime .tooltip {
    font-size: 12px !important;
    padding: 6px 9px !important;
    border-radius: 4px !important;
}

#uptime .foot {
    display: flex;
    justify-content: space-between;
    line-height: 15px;
    color: #999fa6;
}
